@import 'styles/Palette/Palette.scss';
@import 'styles/Fonts/FontScheme.scss';

@mixin textEllipsis() {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.ProjectsCard {
	width: 430px;
	display: flex;
	display: -webkit-flex;
	flex-wrap: wrap;
	overflow-x: hidden;
	overflow-y: auto;
	flex-direction: column;
	justify-content: center;
	word-break: break-all;
	white-space: pre-wrap;
	padding: 1rem 1.7rem;
	margin: 5px 1rem;
	margin-bottom: 2rem;
	box-shadow: 4px 4px 8px rgba($rgbaNavbar, 0.4),
		-6px -6px 8px rgba($rgbaNavbar, 0.4);
	transition: all 0.2s;
	-webkit-transition: all 0.2s;
	color: $white;
	border-radius: 25px;
	cursor: pointer;

	&:hover {
		margin-top: -10px;
	}

	&-Types {
		font-size: $font_22;
	}

	&-Thumbnail {
		width: 100%;
		height: 250px;
		max-height: 250px;
		border-top: 2px solid $gray;
		border-bottom: 2px solid $gray;
		padding: 10px 0;
		margin: 5px 0;
		object-fit: scale-down;
	}

	&-NamePeriod {
		display: flex;
		display: -webkit-flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		margin: 5px 0;

		&-Name {
			max-width: 100%;
			font-size: $font_20;
		}

		&-Period {
			color: $lighterGreen;
			font-size: $font_12;
		}
	}

	&-Stacks {
		max-width: 100%;
		color: $blue;
		font-size: $font_14;
		border-bottom: 1px solid $lighterGray;
		margin-bottom: 8px;
		padding-bottom: 8px;
		@include textEllipsis();
	}
}
