@import 'styles/Palette/Palette.scss';
@import 'styles/Fonts/FontScheme.scss';

@mixin selectedStyle() {
	color: $white;
	background-color: $blue;
}

.History {
	width: 100%;
	height: 100%;

	&-SelectZone {
		flex: 1;
		display: flex;
		display: -webkit-flex;
		justify-content: flex-end;
		align-items: center;

		& > *:focus {
			outline: none;
		}

		& > button {
			width: 80px;
			height: 35px;
			font-size: 14px;
			margin-left: 4px;
			border: none;
			outline: none;
			transition: 0.15s ease-in;
			-webkit-transition: 0.15s ease-in;
			cursor: pointer;

			&:hover {
				@include selectedStyle();
			}
		}

		&-Selected {
			@include selectedStyle();
		}
	}
}
